.dms-share-select,
.dms-share-add {
    flex: 1;
    padding: 0.5rem;
    margin: 0 auto;
    max-width: 640px;
}

.dms-share {
    svg {
        width: 20px;
        height: 20px;
    }

    &-header,
    &-footer {
        flex: 1;
        padding: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-header {
        .title {
            display: flex;
            align-items: center;

            .btn-icon {
                &.select {
                    cursor: default;
                }

                &.add {
                    background-color: white;
                    color: var(--blue-500);
                    border: 1px solid var(--blue-500);
                    transition: cubic-bezier(0.165, 0.84, 0.44, 1);

                    &:hover {
                        color: white;
                        border-color: white;
                        background-color: var(--blue-600);
                    }
                }

                border: 0;
                background-color: var(--blue-600);
                // background-color: #4285f4;
                color: #fff;
                -moz-box-pack: center;
            }

            h2 {
                line-height: 1.75rem;
                font-size: 1.375rem;
                letter-spacing: 0;
                font-weight: 400;
                color: #202124;
                margin: 0;
                margin-left: 1rem;
            }
        }
    }

    .dms-user-item {
        cursor: pointer;
        padding: 0 0.5rem;

        &:hover {
            background-color: rgb(250, 249, 249);
        }
    }
}
